import Log from './log.js'

export default {
  base_url: 'https://srdz.feixianggame.com/icapp/',
  timeout: 30000
}

Log.setEnabled(false)

export function log (str) {
  Log.log(str)
}

export function openLog () {
  Log.setEnabled(true)
}

export function closeLog () {
  Log.setEnabled(false)
}
